import utlis from '../../static/js/utlis.js';
// 引入SDK核心类
import QQMapWX from '../../static/js/qqmap-wx-jssdk.js';
var qqmapsdk = new QQMapWX({
	key: utlis.key // 必填
});
var EARTH_RADIUS = 6378136.49;
export default {
	data() {
		return {
			title: 'map',
			latitude: '', //目的地纬度
			longitude: '', //目的地经度 
			latitudeFinal: '', //目的地纬度
			longitudeFinal: '', //目的地经度 
			polyline: [], //路线
			scale: 14, //缩放级别
			latFrom: '',
			lngFrom: '',
			latTo: '',
			lngTo: '',
			circles: [],
			electricStationList: '',

			markers: [],
			inputValue: '',
			selectRes: '',
			mask1: 0,
			egCode: '1'
		}
	},
	onLoad() {

	},
	onShow() {
		let self = this;
		let egCode = uni.getStorageSync('egCode')
		this.egCode = egCode;
		// 获取现在位置
		uni.getSetting({
			success(res) {
				if (res.authSetting['scope.userLocation'] != undefined && res.authSetting['scope.userLocation'] != true) {
					// 授权失败
					uni.showModal({
						title: '"e享一站"需要获取你的地理位置',
						content: '你的位置将用于地图展示',
						success: function(res) {
							if (res.confirm) {
								uni.openSetting({
									success: (res) => {
										uni.getLocation({
											type: 'gcj02', //返回可以用于uni.openLocation的经纬度
											success: function(res) {
												self.latitude = res.latitude;
												self.longitude = res.longitude;
												self.getData()
											}
										})
									}
								})
							}
						}
					})
				} else if (res.authSetting['scope.userLocation'] == undefined) { //初始化进入
					uni.getLocation({
						type: 'gcj02', //返回可以用于uni.openLocation的经纬度
						success: function(res) {
							self.latitude = res.latitude;
							self.longitude = res.longitude;
							self.getData()
						}
					})
				} else { //授权后默认加载
					uni.getLocation({
						type: 'gcj02', //返回可以用于uni.openLocation的经纬度
						success: function(res) {
							self.latitude = res.latitude;
							self.longitude = res.longitude;
							self.getData()
						}
					})
				}
			}
		});
	},
	methods: {
		mask() {
			this.mask1 = 0
		},
		selectResult() {
			let self = this;
			uni.request({
				url: "https://apis.map.qq.com/ws/place/v1/suggestion",
				data: {
					keyword: self.inputValue,
					// region: '武汉',
					location: '30.49984,114.34253',
					get_subpois: 1,
					page_size: 10,
					key: 'G6HBZ-47UW4-W47U6-DXVNE-HSYDQ-EAFF3'
				},
				success: (res) => {
					//返回搜索结果
					self.selectRes = res.data.data
				}
			})
		},
		// 回到当前位置
		moveToNow() {
			var map = uni.createMapContext('myMap');
			map.moveToLocation();
			// // 获取地图中心位置
			// map.getCenterLocation({
			// 	success(res) {
			// 		console.log(res)
			// 	}
			// });
		},
		// 前往搜索位置
		moveToSelect(a, b) {
			console.log(a, b)
			this.mask1 = 0
			this.latitude = a;
			this.longitude = b;
			this.getData();
		},
		showPowerDetail(e) {
			console.log(e)
			uni.navigateTo({
				url: '/pages/powerDetail/powerDetail' + e.markerId
			})
		},
		onKeyInput: function(event) {
			let self = this;
			this.mask1 = 1
			this.inputValue = event.target.value
			this.selectResult()
		},
		getInput: function(event) {
			let self = this;
			this.mask1 = 1
			this.selectResult()
		},
		warn() {
			uni.navigateTo({
				url: '/pages/charging/charging'
			})
		},
		// 扫码充电
		scan() {
			uni.scanCode({
				success: function(res) {
					console.log('条码类型：' + res.scanType);
					console.log('条码内容：' + res.result);
					let data = {
						custName: uni.getStorageSync('custName'),
						ewmUrl: res.result
					}
					uni.showLoading({
						title: '加载中',
						mask: true
					})
					// 扫码成功开始充电
					utlis.http("POST", 'appChargeStartQRCodeHj', data, function(
						res) {
						if (res.status == "success") {
							uni.setStorageSync('egCode', res.egCode)
							uni.setStorageSync('connectorID', res.connectorID)
							uni.hideLoading()
							uni.showToast({
								title: res.msg,
								duration: 3000,
								mask: true,
								icon: 'success'
							})
							setTimeout(() => {
								uni.navigateTo({
									url: '/pages/charging/charging'
								})
							}, 3000)
						} else {
							uni.hideLoading()
							uni.showToast({
								title: res.data.msg,
								duration: 3000,
								mask: true,
								icon: 'none'
							})
						}
					})
				}
			})
		},
		// 获取电站分布
		getData() {
			let self = this;
			let data = {}
			uni.showLoading({
				title: '加载中',
				mask: true
			})
			utlis.http("POST", 'appElectricStationHj', data, function(res) {
				if (res.status == "success") {
					self.electricStationList = res.electricStationList
					self.markers = [];
					res.electricStationList.forEach(item => {
						let marker = {
							latitude: item.lat,
							longitude: item.lng,
							iconPath: '/static/img/dibiao_icon3x.png',
							width: 30,
							height: 30,
							id: '?id=' + item.esId + '&stationId=' + item.stationId + '&addr=' + item.addr + '&esName=' + item.esName +
								'&lat=' + item.lat + '&lng=' + item.lng,
							iconAppendStr: item.esName,
							iconAppendStrColor: "#55C87D",
							callout: {
								content: item.esName,
								fontize: 16,
								textAlign: 'left',
								bgColor: "#55C87D",
								color: "#fff",
								padding: 10,
								display: "ALWAYS",
								borderRadius: 5,
							}
						}
						self.markers.push(marker)
					})
					console.log(self.markers)
					uni.hideLoading()
				} else {
					uni.hideLoading()
				}
			})
		},
	}
}
